body{
    background-color: rgb(216, 223, 236);
    font-family: Georgia serif;
    padding: 15px;
    color:#333;
    font-family:'Oswald', sans-serif;
}
.formBuilder{
    min-width:480px;
    max-width:1600px;
}
h1{
    margin-left:100px;
    position: relative;

    font-weight: 600;

}
h1::before{
    content: "";
    position: absolute;
    left: 0;
    bottom: -2px;
    height: 3px;
    width: 27px;
    border-radius: 8px;
    background-color: #4070f4;
}
.container{

    padding: 25px;
    background-color: #ffffff;
    border-radius: 5px;
    font-family: 'Caveat', cursive;
    font-family: 'Ms Madi', cursive;
    font-family: 'Oswald', sans-serif;
    width: 350px;
    display: flex; 
    border: 1px solid #333;
    margin-bottom: 10px;

}
p {
    margin:0px
}
.container div{
    
    flex-direction: column;
    justify-content: space-around;
    height: 70px;
    
    
}
.conditionClass{
    display: block;
}
.inputsClass {
    display: flex;
    margin-left: 20px;
    width: 400px;
}
.textsClass {
    display: flex;
}
SELECT {
    width: 100px;
    cursor:pointer;
}
input{
    width:90%;
    margin-bottom: 5px;
    cursor:pointer;
   
}

#selectConditionType{
    width:43%;
    
    margin-right: 10px;

}
#conditionInput, #conditionSelect{
   
    width:43%;
    padding:0px;
}

.childInputClass{

    width: 100%;
}

.addChildButton, .removeButton {
    background-color: #4070f4;
    border: 2px solid rgba(33, 68, 72, 0.59);
    height: 35px;
    width: 60px;
    font-size: 20px;
    margin-right: 10px;
    border:none;
    margin-right: 10px;
    color: white;
    text-align: center;
    font: bold;
